.App {
    text-align: center;
    max-width: 1200px;
    margin: 0 auto;
    padding-left: 15px;
    padding-right: 15px;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

.app-list{
    list-style : none;
}

.player-list{
    margin-top : 1em;
}

.missing-player{
    color : red;
}

.player-name {
    text-align: left;
}

.error-message{
    color: red;
    font-size : 20px;

    margin-top: 1em;
}

.footer {
    margin-top : 1em;
}

.stats-list, .footer{
    font-size: 1vw;
}

.app-header, .list-header {
    font-size: 2vw;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
